import React, { Component } from 'react'
import Batting from './Batting'

export default class Toss extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             tossCountry: "",
             tossLostCountry: "",
             tossDC: null,
             battingCountry: null,
             bowlingCountry: null,
             flipVisibility1: false,
             flipVisibility2: false,
             targetRun: 0
        }
        this.clickAction = this.clickAction.bind(this)
        //this.updateTossAction = this.updateTossAction.bind(this)
        this.batAction = this.batAction.bind(this)
        this.bowlAction = this.bowlAction.bind(this)
        this.inningsEnd = this.inningsEnd.bind(this)
    }
    
    batAction(){
        this.setState((state)=>({
            battingCountry: state.tossCountry,
            bowlingCountry: state.tossLostCountry,
            flipVisibility1: true
        }))
    }

    bowlAction(){
        this.setState((state)=>({
            battingCountry: state.tossLostCountry,
            bowlingCountry: state.tossCountry,
            flipVisibility1: true
        }))
    }

    clickAction(){
        this.setState({
            tossDC: Math.floor(Math.random() * 2)
        })
        /* this.setState({
            tossDA: Math.floor(Math.random() * 2)
        }) */
        if(this.state.tossDC === 1){
            this.setState({
                tossCountry: this.props.T1,
                tossLostCountry: this.props.T2
            })
        }
        else{
            this.setState({
                tossCountry: this.props.T2,
                tossLostCountry: this.props.T1
            })
        }
        
          
    }

    /* t1action() {
        return(
            <Batting batCountry={this.props.T1}/>
        )
    }

    t2action() {
        return(
            <Batting batCountry={this.props.T2}/>
        )
    } */

    inningsEnd(target){
        this.setState({
            flipVisibility2: true,
            targetRun: target
        })
    }

    render() {
        return (
            <div>
                <h1>{this.props.T1} vs {this.props.T2}</h1>
                <button className='btn btn-primary' onClick={this.clickAction}>Toss Coin</button>
                {this.state.tossCountry !== ''? <div><h2>{this.state.tossCountry} won the toss and chose to?</h2>
                <button className='btn btn-primary tossOption' onClick={this.batAction}>Bat</button>
                <button className='btn btn-primary tossOption' onClick={this.bowlAction}>Bowl</button></div>:''}
                <div className="inningsClass">
                    {this.state.flipVisibility1? <Batting className='innings1' batCountry={this.state.battingCountry} targetRuns={this.inningsEnd} battingFirst={true}></Batting>:''}
                    {this.state.flipVisibility2? <Batting className='innings2' batCountry={this.state.bowlingCountry}
                    bowlCountry={this.state.battingCountry} chaseTarget={this.state.targetRun} battingFirst={false}></Batting>:''}
                </div>
            </div>
        )
    }
}
