import React, { Component } from 'react'
import Toss from './Toss'


export default class TeamSelection extends Component {
    constructor(props) {
        super(props)
        
        this.countries = ["India","Australia","England","New Zealand","South Africa","Pakistan","Bangladesh","Sri Lanka"]

        this.state = {
             T1: "",
             T2: "",
        }

        this.submitAction = this.submitAction.bind(this)
    }

    submitAction(e){
        e.preventDefault()
        let t1storage = document.getElementById('team1')
        let t1Text = t1storage.options[t1storage.selectedIndex].value
        let t2storage = document.getElementById('team2')
        let t2Text = t2storage.options[t2storage.selectedIndex].value
        this.setState({
            T1: t1Text,
            T2: t2Text
        })
    }

    render() {
        return (
            <div>
                <h2>2 overs match with 3 wickets each.</h2>
                <h4>Select the teams</h4>
                <form action="" id='team-form' onSubmit={this.submitAction}>
                  <div id='forFlex'>
                    <select className="form-select" name='team-form' id='team1' form="team-form" required>
                        {this.countries.map((country)=>{
                            return <option value={country}>{country}</option>
                        })}
                    </select>
                    <select className="form-select" name='team-form' id='team2' form='team-form' required>
                        {this.countries.map((country)=>{
                            return <option value={country}>{country}</option>
                        })}
                    </select>
                  </div>
                  <input type="submit" value="Save" className='btn btn-primary' />
                </form>
                {this.state.T1 !== ''? <Toss T1={this.state.T1} T2={this.state.T2}></Toss> :''}
            </div>
        )
    }
}
