import React, { Component } from 'react'

export default class Batting extends Component {
    constructor(props) {
        super(props)
        this.runs = [0,1,2,4,6]
        this.state = {
             totRuns: 0,
             wickets: 0,
             curRun: null,
             balls: 0,
             targetVisibility: false,
             result: "",
             resultVisibility: false
        }
        this.flipPages = this.flipPages.bind(this)
    }
    
    flipPages(){
        if(!this.props.battingFirst && this.state.totRuns >= this.props.chaseTarget){
            //alert(`${this.props.batCountry} won by ${3-this.state.wickets} wickets`)
            this.setState((state,props)=>({
                result: `${props.batCountry} won by ${3 - state.wickets} wickets`,
                resultVisibility: true
            }))
        }
        else if(this.state.balls<12 && this.state.wickets<3){
            let addRun = this.runs[Math.floor(Math.random() * 5)]
            if(addRun){
                this.setState((state)=>({
                    curRun: addRun,
                    totRuns: state.totRuns + addRun,
                    balls: state.balls + 1
                }))
            }
            else{
                this.setState((state)=>({
                    curRun: addRun,
                    wickets: state.wickets + 1,
                    balls: state.balls + 1
                }))
            }
        }
        else{
            if(this.props.battingFirst){
                this.setState({
                    targetVisibility: true
                })
                this.props.targetRuns(this.state.totRuns + 1)
            }
            else{
                if(this.state.totRuns >= this.props.chaseTarget){
                    this.setState((state,props)=>({
                        result: `${props.batCountry} won by ${3 - state.wickets} wickets`,
                        resultVisibility: true
                    }))
                }
                else if(this.state.totRuns === (this.props.chaseTarget - 1)){
                    this.setState({
                        result: "Match tied"
                    })
                }
                else{
                    this.setState((state,props)=>({
                        result: `${props.bowlCountry} won by ${props.chaseTarget - state.totRuns - 1} runs`,
                        resultVisibility: true
                    }))
                }

                /* this.state.totRuns >= this.props.chaseTarget? alert(`${this.props.batCountry} won by ${3-this.state.wickets} wickets`): 
                alert(`${this.props.bowlCountry} won by ${this.props.chaseTarget - this.state.totRuns - 1} runs`) */
            }
            alert("Innings ended")
        }
    }

    render() {
        return (
            <div>
                <button className='btn btn-success' onClick={this.flipPages}>Flip Pages</button>
                <h3>{this.props.batCountry}</h3>
                <h3>{this.state.totRuns}/{this.state.wickets}</h3>
                <h4>{Math.floor(this.state.balls/6)}.{this.state.balls % 6} overs</h4>
                {this.state.curRun === 0? <h3>OUT!</h3>:<h3>{this.state.curRun}!</h3>}
                {this.state.targetVisibility? <h4>Target: {this.state.totRuns + 1}</h4>: ''}
                {this.state.resultVisibility? <h4>{this.state.result}</h4>:''}
            </div>
        )
    }
}
